// Component for viewing debate history
import { useState } from 'react';
import { History, Download, Search, Calendar, Trophy, FileText, Trash2, ArrowLeft } from 'lucide-react';
import { useDebateHistory } from './useDebateHistory';
import { DebateSession } from './debate';

interface DebateHistoryProps {
  onBack: () => void;
}

export function DebateHistory({ onBack }: DebateHistoryProps) {
  const { history, isLoading, deleteDebate } = useDebateHistory();
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedEntry, setSelectedEntry] = useState<string | null>(null);

  const filteredHistory = history.filter(entry =>
    entry.session.motion.motion.toLowerCase().includes(searchTerm.toLowerCase()) ||
    entry.session.motion.category.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const downloadTranscript = (transcript: string, motion: string, date: Date) => {
    const filename = `debate-transcript-${date.toISOString().split('T')[0]}.txt`;
    const content = `DEBATE TRANSCRIPT
Motion: ${motion}
Date: ${date.toLocaleDateString()}
Time: ${date.toLocaleTimeString()}

${transcript}`;

    const blob = new Blob([content], { type: 'text/plain' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = filename;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  const formatDate = (date: Date) => {
    return new Intl.DateTimeFormat('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    }).format(date);
  };

  const getWinnerDisplay = (session: DebateSession) => {
    if (!session.result) return 'No result';
    const humanSide = session.participants.human.side;
    const humanWon = session.result.winner === humanSide;
    return humanWon ? '🏆 You Won' : '😔 You Lost';
  };

  if (isLoading) {
    return (
      <div className="bg-white border border-gray-200 rounded-lg shadow-sm p-8">
        <div className="flex items-center justify-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-blue-600 mr-3"></div>
          <span className="text-lg text-gray-600">Loading debate history...</span>
        </div>
      </div>
    );
  }

  return (
    <div className="bg-white border border-gray-200 rounded-lg shadow-sm">
      {/* Header */}
      <div className="p-6 border-b border-gray-200">
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center gap-3">
            <button
              onClick={onBack}
              className="p-2 text-gray-600 hover:text-gray-900 hover:bg-gray-100 rounded-lg transition-colors"
            >
              <ArrowLeft className="w-5 h-5" />
            </button>
            <History className="w-6 h-6 text-blue-600" />
            <h2 className="text-xl font-semibold text-gray-900">Debate History</h2>
          </div>
          <div className="text-sm text-gray-500">
            {history.length} debate{history.length !== 1 ? 's' : ''} completed
          </div>
        </div>

        {/* Search */}
        <div className="relative">
          <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-4 h-4" />
          <input
            type="text"
            placeholder="Search debates by motion or category..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
          />
        </div>
      </div>

      {/* History List */}
      <div className="p-6">
        {filteredHistory.length === 0 ? (
          <div className="text-center py-12">
            <History className="w-16 h-16 text-gray-300 mx-auto mb-4" />
            <h3 className="text-lg font-medium text-gray-900 mb-2">
              {history.length === 0 ? 'No debates yet' : 'No matching debates'}
            </h3>
            <p className="text-gray-600">
              {history.length === 0 
                ? 'Complete your first debate to see it here'
                : 'Try adjusting your search terms'
              }
            </p>
          </div>
        ) : (
          <div className="space-y-4">
            {filteredHistory.map((entry) => (
              <div
                key={entry.id}
                className="border border-gray-200 rounded-lg p-4 hover:shadow-md transition-shadow"
              >
                <div className="flex items-start justify-between">
                  <div className="flex-1">
                    <h3 className="font-medium text-gray-900 mb-2">
                      {entry.session.motion.motion}
                    </h3>
                    
                    <div className="grid grid-cols-2 md:grid-cols-4 gap-4 text-sm text-gray-600 mb-3">
                      <div className="flex items-center gap-1">
                        <Calendar className="w-4 h-4" />
                        {formatDate(entry.savedAt)}
                      </div>
                      <div className="flex items-center gap-1">
                        <Trophy className="w-4 h-4" />
                        {getWinnerDisplay(entry.session)}
                      </div>
                      <div>
                        Category: {entry.session.motion.category}
                      </div>
                      <div>
                        Your role: {entry.session.participants.human.role.name}
                      </div>
                    </div>

                    {/* Scores */}
                    {entry.session.result && (
                      <div className="flex items-center gap-4 text-sm">
                        <span className="text-blue-600 font-medium">
                          Government: {entry.session.result.score.government.toFixed(1)}
                        </span>
                        <span className="text-red-600 font-medium">
                          Opposition: {entry.session.result.score.opposition.toFixed(1)}
                        </span>
                      </div>
                    )}
                  </div>

                  {/* Actions */}
                  <div className="flex items-center gap-2 ml-4">
                    <button
                      onClick={() => downloadTranscript(
                        entry.transcript,
                        entry.session.motion.motion,
                        entry.savedAt
                      )}
                      className="p-2 text-gray-600 hover:text-blue-600 hover:bg-blue-50 rounded-lg transition-colors"
                      title="Download transcript"
                    >
                      <Download className="w-4 h-4" />
                    </button>
                    
                    <button
                      onClick={() => setSelectedEntry(
                        selectedEntry === entry.id ? null : entry.id
                      )}
                      className="p-2 text-gray-600 hover:text-green-600 hover:bg-green-50 rounded-lg transition-colors"
                      title="View details"
                    >
                      <FileText className="w-4 h-4" />
                    </button>
                    
                    <button
                      onClick={() => {
                        if (confirm('Are you sure you want to delete this debate?')) {
                          deleteDebate(entry.id);
                        }
                      }}
                      className="p-2 text-gray-600 hover:text-red-600 hover:bg-red-50 rounded-lg transition-colors"
                      title="Delete debate"
                    >
                      <Trash2 className="w-4 h-4" />
                    </button>
                  </div>
                </div>

                {/* Expanded Details */}
                {selectedEntry === entry.id && (
                  <div className="mt-4 pt-4 border-t border-gray-200">
                    <div className="space-y-4">
                      {/* Motion Context */}
                      <div>
                        <h4 className="font-medium text-gray-900 mb-1">Context</h4>
                        <p className="text-sm text-gray-600">{entry.session.motion.context}</p>
                      </div>

                      {/* Transcript Preview */}
                      <div>
                        <h4 className="font-medium text-gray-900 mb-1">Transcript Preview</h4>
                        <div className="bg-gray-50 p-3 rounded text-sm text-gray-700 max-h-32 overflow-y-auto">
                          {entry.transcript.substring(0, 300)}...
                        </div>
                      </div>

                      {/* Detailed Results */}
                      {entry.session.result && (
                        <div>
                          <h4 className="font-medium text-gray-900 mb-2">Detailed Results</h4>
                          <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
                            <div>
                              <div className="font-medium text-gray-700 mb-1">Key Moments</div>
                              <ul className="space-y-1">
                                {entry.session.result.keyMoments.slice(0, 3).map((moment, idx) => (
                                  <li key={idx} className="text-gray-600">• {moment}</li>
                                ))}
                              </ul>
                            </div>
                            <div>
                              <div className="font-medium text-gray-700 mb-1">Improvement Areas</div>
                              <ul className="space-y-1">
                                {entry.session.result.improvementAreas.slice(0, 3).map((area, idx) => (
                                  <li key={idx} className="text-gray-600">• {area}</li>
                                ))}
                              </ul>
                            </div>
                          </div>
                        </div>
                      )}
                    </div>
                  </div>
                )}
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}